export function createMemberAbout(
  httpClient: HttpClient
): CreateMemberAboutSignature {
  return (memberAbout: NonNullablePaths<MemberAbout, `content`>) =>
    universalCreateMemberAbout(
      memberAbout,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateMemberAboutSignature {
  /**
   * Creates the "About" section content for a member.
   * @param - The "About" section content to create.
   * @returns The created "About" section content.
   */
  (memberAbout: MemberAbout): Promise<MemberAbout>;
}

export function updateMemberAbout(
  httpClient: HttpClient
): UpdateMemberAboutSignature {
  return (
    _id: string,
    memberAbout: NonNullablePaths<UpdateMemberAbout, `content` | `revision`>
  ) =>
    universalUpdateMemberAbout(
      _id,
      memberAbout,
      // @ts-ignore
      { httpClient }
    );
}

interface UpdateMemberAboutSignature {
  /**
   * Updates the "About" section content for a member.
   *
   * Each time the "About" section content is updated, `revision` increments by 1. The existing `revision` must be included when updating the "About" section content. This ensures you're working with the latest "About" section and prevents unintended overwrites.
   * @param - ID of the "About" section content.
   * @returns The updated "About" section content.
   */
  (_id: string, memberAbout: UpdateMemberAbout): Promise<MemberAbout>;
}

